import React, { Component } from "react";
import parse from 'react-html-parser';
import { STRING_TO_URL_CONVERT } from '../../../utils'
import { v4 as uuidv4 } from 'uuid';
const reactStringReplace = require("react-string-replace");
const convertCss = (hashtag_color, match, isHash) => {
    let style = `<span className="gridhashtag" style="color:${hashtag_color};font-weight:bold;">${isHash ? `#` : ``}${match}</span>`
    return style;
}

const SlackContent = ({ fdata, content, personalization, unicodeyEmojiConvert }) => <React.Fragment>

    {personalization.hashtag_highlight === 1 &&
        personalization.hashtag_feed === 0 &&
        personalization.hashtag_all === 0
        ? unicodeyEmojiConvert(content)
        : personalization.hashtag_highlight === 1 &&
            personalization.hashtag_feed === 0 &&
            personalization.hashtag_all === 1
            ? unicodeyEmojiConvert(reactStringReplace(
                content,
                /#(\w+)/g,
                (match, i) => convertCss(personalization.hashtag_color, match, true)
            ))
            : personalization.hashtag_highlight === 1 &&
                personalization.hashtag_feed === 1 &&
                personalization.hashtag_all === 0
                ? unicodeyEmojiConvert(reactStringReplace(
                    content,
                    fdata.hash.hashString,
                    (match, i) => convertCss(personalization.hashtag_color, match, false)
                ))
                : personalization.hashtag_highlight === 1 &&
                    personalization.hashtag_feed === 1 &&
                    personalization.hashtag_all === 1
                    ? unicodeyEmojiConvert(reactStringReplace(
                        content,
                        /#(\w+)/g,
                        (match, i) => convertCss(personalization.hashtag_color, match, true)
                    ))
                    : unicodeyEmojiConvert(content)}
</React.Fragment>

const ContentWithOutSlack = ({ fdata, content, personalization }) => <React.Fragment>
    {personalization.hashtag_highlight === 1 &&
        personalization.hashtag_feed === 0 &&
        personalization.hashtag_all === 0
        ? parse(content)
        : personalization.hashtag_highlight === 1 &&
            personalization.hashtag_feed === 0 &&
            personalization.hashtag_all === 1
            ? reactStringReplace(
                parse(content),
                /#(\w+)/g,
                (match, i) => (
                    <span key={uuidv4()}
                        className="gridhashtag"
                        style={{
                            color: personalization.hashtag_color,
                            fontWeight: "bold",
                        }}
                    >

                        #{match}
                    </span>
                )
            )
            : personalization.hashtag_highlight === 1 &&
                personalization.hashtag_feed === 1 &&
                personalization.hashtag_all === 0
                ? reactStringReplace(
                    parse(content),
                    fdata.hash.hashString,
                    (match, i) => (
                        <span key={uuidv4()}
                            className="gridhashtag"
                            style={{
                                color: personalization.hashtag_color,
                                fontWeight: "bold",
                            }}
                        >

                            {match}
                        </span>
                    )
                )
                : personalization.hashtag_highlight === 1 &&
                    personalization.hashtag_feed === 1 &&
                    personalization.hashtag_all === 1
                    ? reactStringReplace(
                        parse(content),
                        /#(\w+)/g,
                        (match, i) => (
                            <span key={uuidv4()}
                                className="gridhashtag"
                                style={{
                                    color: personalization.hashtag_color,
                                    fontWeight: "bold",
                                }}
                            >

                                #{match}
                            </span>
                        )
                    )
                    : parse(content)}
</React.Fragment>

const findHaseTagIndex = (data) => {
    let FindHasheTag = [];
    for (var i = 0; i < data.length; i++) {
        if (data[i] === "#") FindHasheTag.push(i);
    }
    return FindHasheTag;
}
const findWhiteSpace = (startFrom, content) => {
    let value = 0;

    for (; startFrom < content.length; startFrom++) {
        if (content[startFrom] == " " || content[startFrom] == '') {
            return value = startFrom; break;
        }
    }
    if (value === 0 && content.length < 210) value = content.length;
    else value = 200;
    return startFrom == undefined ? 200 : value;

}

class ContentConversion extends Component {
    state = {
        isTrimEnable: false
    }
    unicodeyEmojiConvert = (data) => {
        //return <Emojify>{parse(data)}</Emojify>
        return data
    }
    onClickReadText = event => {
        const { isTrimEnable } = this.state;
        this.setState({ isTrimEnable: !isTrimEnable });
    }

    render() {
        const { fdata, contentData, fullcontentData, personalization } = this.props;
        
        let trimContent = 0; let content = ""; let hashTagFindContent = ""; let fullcontent = fdata.fullcontent;
        if (fdata.networkId != 20 && fdata.content.length > 200) {
            hashTagFindContent = String(contentData).substring(0, 200);
            let lastHashTag = findHaseTagIndex(hashTagFindContent);
            let hashTagLimit = findWhiteSpace(lastHashTag[lastHashTag.length - 1], fdata.content)
            content = String(contentData).substring(0, parseInt(hashTagLimit));
            fullcontent = String(fullcontentData).substring(parseInt(hashTagLimit), fdata.content.length);

        }
        else {
            content = fdata.content;
            fullcontent = fdata.content;
        }
        content = this.state.isTrimEnable ? STRING_TO_URL_CONVERT(fullcontentData)  : STRING_TO_URL_CONVERT(content);
        fullcontent = STRING_TO_URL_CONVERT(fullcontent);
    return fdata.networkId === 20 ? <React.Fragment><SlackContent fdata={fdata} content={fullcontent} personalization={personalization} unicodeyEmojiConvert={this.unicodeyEmojiConvert} />

{
    fdata.content.length > 200 ? <span className="morecontent readMore"><span>{this.state.isTrimEnable ? <SlackContent fdata={fdata} content={window.slackdown.parse(fullcontent)} personalization={personalization} unicodeyEmojiConvert={this.unicodeyEmojiConvert} /> : null}
        </span>
        {!this.state.isTrimEnable ? <a className="morelink" onClick={this.onClickReadText}> Read More...</a> : null}</span> : null
}
</React.Fragment> : <React.Fragment><ContentWithOutSlack fdata={fdata} content={content} personalization={personalization} />
    {
         fdata.content.length > 200 ?  <span className="morecontent readMore"><span>{this.state.isTrimEnable ? <ContentWithOutSlack fdata={fdata} content={fullcontent} personalization={personalization} /> : null} </span>
            {!this.state.isTrimEnable ? <a className="morelink" onClick={this.onClickReadText}> Read More...</a> : null}</span>  : null
    }


    {this.state.isTrimEnable ? <a className="lesslink" onClick={this.onClickReadText}> Read Less</a> : null}
</React.Fragment>

    }

}

export default ContentConversion;